import api from './api';

class UserService {
  getPublicContent() {
    return api.get('/test/all');
  }

  getUserBoard() {
    return api.get('/test/user');
  }

  getStaffBoard() {
    return api.get('/test/staff');
  }

  getAdminBoard() {
    return api.get('/test/admin');
  }

  // user data
  
  getAll() {
    return api.get('/admin/usermgmt/users')
  }

  get(id) {
    return api.get(`/admin/usermgmt/users/${id}`)
  }

  create(data) {
    return api.post('/admin/usermgmt/users', data)
  }

  update(id, data) {
    return api.put(`/admin/usermgmt/users/${id}`, data)
  }

  delete(id) {
    return api.delete(`/admin/usermgmt/users/${id}`)
  }
}

export default new UserService();
